.header {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.header__logo-wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-height: 65px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: white;
  border-bottom: 1px solid #e6e6e6; }

.header__logo {
  width: 191px;
  cursor: pointer; }
  .header__logo:hover:not(.header__logo--active) {
    opacity: 0.6; }
  .header__logo:active:not(.header__logo--active) {
    opacity: 0.3; }
  .header__logo--active {
    cursor: auto; }

.header__logo-img {
  width: 100%;
  height: auto; }

.header__toggle {
  position: relative;
  width: 24px;
  height: 20px;
  margin-bottom: 5px;
  padding: 0;
  background-color: white;
  border: 0;
  cursor: pointer; }
  .header__toggle::before, .header__toggle::after {
    content: "";
    position: absolute;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: #222222; }
  .header__toggle--opened::before {
    /*closed*/
    transform: rotate(45deg); }
  .header__toggle--opened::after {
    /*closed*/
    transform: rotate(-45deg); }
  .header__toggle--closed::before {
    /*opened*/
    top: 0;
    background-color: #222222;
    box-shadow: 0 9px 0 0 #222222; }
  .header__toggle--closed::after {
    /*opened*/
    bottom: 0; }
  .header__toggle--nojs {
    display: none; }

